    body {
    font-family:sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
    background-color: #f0f0f0;
    background: url(images/grocery.jpg);
  }
  
  .todo-app {
    
    padding: 20px;  
    width: 400px;
    margin-top:3px; 
    color:crimson;
  }
  
  h1 {
    font-size:45px;
    text-align: center;
    margin-bottom: 10px;
    color:chartreuse;
  }

  h2{
    font-size: 25px;
    text-align: center;
  }
  
  input {
    width: calc(100% - 20px);
    padding: 10px;
    margin-bottom: 10px;
    font-size: 16px;
  }
  
  button {
    width: 25%;
    background-color:black;
    color:slategray;
    border:none;
    outline:none;
    cursor: pointer;
    font-size: 16px;
    margin-top: 10px;
    margin-left: 140px;
    padding: 5px 10px;
    justify-content: center;
    align-items: center;
    
  }
  
  button:hover {
    background-color:chartreuse;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background-color:antiquewhite;
    margin-bottom: 10px;
    border-radius: 4px;
  }
  
  li button {
    background-color:black;
    margin-left:80px;
    font-size: 14px;
    padding: 3px 8px;
    cursor: pointer;
  }
  
  li button:hover {
    background-color:coral;
  }
  